.project-list {
  padding: 100px 0 80px;
  background: var(--bg-primary);
  position: relative;
}

.project-list::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 200px;
  background: var(--gradient-secondary);
  opacity: 0.3;
  z-index: -1;
}

.project-list h2 {
  text-align: center;
  margin-bottom: 60px;
  color: var(--text-primary);
  font-size: clamp(2rem, 5vw, 2.8rem);
  font-weight: 800;
  transition: var(--transition-normal);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
}

.project-list h2::after {
  content: '';
  position: absolute;
  bottom: -16px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: var(--gradient-primary);
  border-radius: var(--radius-sm);
}

.category-title {
  font-size: clamp(1.4rem, 3vw, 1.8rem);
  font-weight: 700;
  color: var(--text-primary);
  text-align: center;
  margin: 60px 0 40px;
  position: relative;
  cursor: pointer;
  transition: var(--transition-normal);
  padding: 16px 32px;
  border-radius: var(--radius-lg);
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  display: inline-block;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  box-shadow: var(--shadow-md);
}

.category-title::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-primary);
  border-radius: var(--radius-lg) var(--radius-lg) 0 0;
}

.category-title:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  background: var(--primary-color);
  color: white;
}

/* Featured Projects Section */
.featured-projects-section {
  margin-bottom: 80px;
  position: relative;
}

.featured-title {
  font-size: clamp(1.6rem, 3.5vw, 2rem);
  font-weight: 700;
  color: var(--text-primary);
  text-align: center;
  margin: 0 0 50px;
  position: relative;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.featured-title::after {
  content: '';
  position: absolute;
  bottom: -12px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: var(--radius-sm);
}

.featured-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  padding: 0 20px;
  margin: 0 auto;
  max-width: 1600px;
}

/* Other Projects Section */
.other-projects-section {
  margin-top: 80px;
  padding-top: 40px;
  border-top: 2px solid var(--border-color);
  position: relative;
}

.other-projects-title {
  font-size: clamp(1.4rem, 3vw, 1.8rem);
  font-weight: 600;
  color: var(--text-secondary);
  text-align: center;
  margin: 0 0 40px;
}

/* Hover Description Display - Fixed on Right Side */
.project-description-display {
  position: fixed;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  width: 380px;
  padding: 28px;
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-lg);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  z-index: 100;
  max-height: 70vh;
  overflow-y: auto;
}

.project-description-display.visible {
  opacity: 1;
}

.hover-description {
  color: var(--text-primary);
  font-size: 0.95rem;
  line-height: 1.65;
  margin: 0;
}

.hover-description.placeholder {
  display: none;
}

/* Project Categories */
.project-category {
  margin-bottom: 45px;
}

.category-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin: 0 0 16px 20px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  opacity: 0.8;
}

.project-links {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}

.project-link {
  display: block;
  padding: 16px 24px;
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  color: var(--text-primary);
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: var(--transition-normal);
  box-shadow: var(--shadow-sm);
}

.project-link:hover {
  transform: translateX(8px);
  border-color: var(--primary-color);
  box-shadow: var(--shadow-md);
  background: var(--bg-secondary);
  color: var(--primary-color);
}

.project-link::before {
  content: 'â†’ ';
  opacity: 0;
  transition: var(--transition-normal);
  margin-right: 8px;
}

.project-link:hover::before {
  opacity: 1;
}

/* Enhanced grid layout - Legacy styles for reference, can be removed if not used elsewhere */
.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 40px;
  padding: 0 20px;
  margin: 0 auto;
  max-width: 1400px;
}

.project-grid .project-card {
  animation-delay: calc(var(--card-index, 0) * 0.1s);
}

@media (max-width: 1400px) {
  .featured-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 28px;
  }
}

@media (max-width: 1200px) {
  .featured-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
  }
  
  .project-grid {
    grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
    gap: 32px;
  }
}

@media (max-width: 768px) {
  .featured-grid {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 0 16px;
  }
  
  .project-grid {
    grid-template-columns: 1fr;
    gap: 24px;
    padding: 0 16px;
  }
  
  .featured-projects-section {
    margin-bottom: 60px;
  }
  
  .project-list {
    padding: 60px 0;
  }
  
  .featured-title {
    font-size: 1.6rem;
    margin-bottom: 30px;
  }
  
  .other-projects-title {
    font-size: 1.4rem;
    margin-bottom: 30px;
    padding-top: 30px;
  }

  .other-projects-section {
    margin-top: 60px;
    padding-top: 30px;
  }

  .project-links {
    padding: 0 16px;
  }

  .project-link {
    padding: 14px 20px;
    font-size: 1rem;
  }

  .project-description-display {
    display: none;
  }

  .category-title {
    font-size: 1.2rem;
    margin: 0 0 16px 16px;
  }

  .project-category {
    margin-bottom: 40px;
  }
}
