.featured-project-card {
  display: flex;
  flex-direction: column;
  border-radius: var(--radius-xl);
  background: var(--bg-card);
  box-shadow: var(--shadow-md);
  transition: var(--transition-normal);
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  margin: 0;
  border: 1px solid var(--border-color);
  position: relative;
  opacity: 0;
  transform: translateY(30px);
}

.featured-project-card.animate-in {
  animation: slideInUp 0.6s ease-out forwards;
}

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.featured-project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-primary);
  z-index: 1;
  transform: scaleX(0);
  transition: var(--transition-normal);
}

.featured-project-card:hover::before {
  transform: scaleX(1);
}

.featured-project-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl);
  border-color: var(--primary-color);
}

.featured-project-image {
  height: 280px;
  object-fit: cover;
  width: 100%;
  transition: var(--transition-slow);
  opacity: 0.5;
  filter: blur(2px);
}

.featured-project-image.loaded {
  opacity: 1;
  filter: blur(0);
}

.featured-project-card:hover .featured-project-image {
  transform: scale(1.05);
}

.featured-project-content {
  padding: 28px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  flex-grow: 1;
}

.featured-project-content h3 {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 700;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.3;
}

.featured-project-content p {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 15px;
  margin: 0;
}

.featured-project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 4px;
}

.featured-project-tag {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 6px 12px;
  border-radius: var(--radius-lg);
  font-size: 13px;
  font-weight: 600;
  border: 1px solid var(--border-color);
  transition: var(--transition-fast);
}

.featured-project-tag:hover {
  background: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}

.featured-project-links {
  margin-top: auto;
  display: flex;
  gap: 24px;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
}

.featured-text-link {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 600;
  font-size: 15px;
  transition: var(--transition-fast);
  position: relative;
  padding-bottom: 2px;
}

.featured-text-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--primary-color);
  transition: var(--transition-fast);
}

.featured-text-link:hover {
  color: var(--primary-color);
}

.featured-text-link:hover::after {
  width: 100%;
}

@media (max-width: 768px) {
  .featured-project-card {
    width: 100%;
  }

  .featured-project-content {
    padding: 24px;
  }

  .featured-project-content h3 {
    font-size: 1.3rem;
  }

  .featured-project-image {
    height: 240px;
  }
}
